import React from 'react'
import { Redirect, RouteComponentProps } from 'react-router'

import { useRoot } from '../../../atoms/RootProvider'
import { useEffectOnce } from '../../../hooks/useEffectOnce'
import { i18n } from '../../../i18n'
import {
  IPasswordChangeProps,
  PasswordChangePage,
} from '../pages/PasswordChangePage'

export const PasswordChangeRoute: React.SFC<
  RouteComponentProps<{ token: string }>
> = ({ history, location, match }) => {
  const { auth } = useRoot()

  useEffectOnce(() => {
    auth.invalidate()
    auth.setForPasswordChange(match.params.token)
  })

  const props: IPasswordChangeProps = {
    onPasswordChange: async (password: string) => {
      await auth.changePassword(password)
      history.replace('/')
    },
    pageTitle: '',
    buttonLabel: '',
  }

  if (location.pathname.includes('register')) {
    props.pageTitle = i18n.t`Set up password`
    props.buttonLabel = i18n.t`Create password`
  } else if (location.pathname.includes('recovery')) {
    props.pageTitle = i18n.t`Password recovery`
    props.buttonLabel = i18n.t`Update password`
  } else {
    return <Redirect to="/" />
  }
  return <PasswordChangePage {...props} />
}

export default PasswordChangeRoute
